import CloudStorage, { CLOUD_LIST } from '../components/CloudStorage'
import { observer, inject, PropTypes } from 'mobx-react'
import { Button, Avatar, Select } from 'antd'
import Legend from '../components/Legend'
import setting from '../utils/setting'
import user from '../stores/user'
import '../styles/setting.scss'
import { t } from 'i18next'
import React from 'react'

const CloudSelect = CLOUD_LIST.map(({ name, key, disable }) => {
  return <Select.Option disabled={disable} value={key} key={key}>{name}</Select.Option>
})

class GrantAuthorizationButton extends React.Component {
  auth = () => {
    window.location.href = `${process.env.API}/auth/google`
  }

  render () {
    const { auth } = this
    return (
      <div className="auth">
        <div align="center">
          <p>{t('auth.tips')}</p>
          <Button onClick={auth} type="primary" ghost>Google Authorize</Button>
        </div>
      </div>
    )
  }
}

@inject('user')
@observer
export default class Setting extends React.Component {
  static propTypes = {
    user: PropTypes.observableObject
  }

  changeDefaultUploadServer = preference => {
    setting({ options: { preference } }).then(() => {
      user.changeOptionsPreference(preference)
    })
  }

  render () {
    const { props: { user: { id, displayName = '****', image, options = {} } } } = this

    const Setting = (
      <React.Fragment>
        <Legend title="User Info">
          <section className="user-info">
            <Avatar src={image} />
            <p>
              <span>User ID:</span>
              <span>{id}</span>
            </p>
            <p>
              <span>User Name:</span>
              <span>{displayName}</span>
            </p>
          </section>
        </Legend>
        <Legend title="Client Setting">
          <section className="setting-table">
            <div>
              <div>Default Upload Server:</div>
              <div>
                <Select
                  defaultValue={options.preference || 'default'}
                  style={{ width: 120 }}
                  onChange={this.changeDefaultUploadServer}>
                  {CloudSelect}
                </Select>
              </div>
            </div>
          </section>
        </Legend>
        <Legend title="Cloud Setting">
          <React.Fragment>
            <CloudStorage />
          </React.Fragment>
        </Legend>
        <Legend title="Source Code">
          <div className="source-code">
            <a href="https://github.com/Army-U/avi">Client Source Code</a>
            <span>|</span>
            <a href="https://github.com/Army-U/avi-api">Server Source Code</a>
          </div>
        </Legend>
      </React.Fragment>
    )

    return (
      <section className="setting">
        <div className="container">
          {id && Setting}
          {!id && <GrantAuthorizationButton />}
        </div>
      </section>
    )
  }
}



