import storage from '../storage/pictures'
import user from '../stores/user'
import prefetch from './prefetch'
import token from './token'
import axios from 'axios'
import copy from './copy'

export function smms (file, config) {
  const formData = new FormData()
  formData.append('smfile', file)
  return axios.post(process.env.SMMS, formData, config)
}

export function avi (file, config) {
  const formData = new FormData()
  formData.append(typeof file === 'string' ? 'url' : 'file', file)
  formData.append('type', user.options.preference)
  return axios.post(process.env.API + '/upload', formData, config)
}

const whitelist = [undefined, '', 'default']

export default (file, config) => {
  return new Promise(resolve => {
    const request = !user.id || whitelist.includes(user.options.preference)
      ? smms(file, config)
      : avi(file, config)

    request
      .then(({ data: { data: { url } } }) => {
        copy(url)
        storage.add({ url, token: token.token })
        resolve({ url })
        prefetch(url)
      }, err => { if (!axios.isCancel(err)) throw err })
  })
}



